
'use client';

import React, { useEffect, useState, useTransition, useCallback } from 'react';
import { useRefresh } from '@/hooks/use-refresh';
import { Card, CardContent, CardHeader } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { Search, Trash, Loader2, SearchX, FileDown, Eye } from 'lucide-react';
import { getDatFiles } from '@/lib/actions';
import type { DatFile } from '@/lib/schemas';
import { useToast } from '@/hooks/use-toast';
import { format } from 'date-fns';
import { Checkbox } from '@/components/ui/checkbox';
import { Badge } from '@/components/ui/badge';

function DatFilesContent() {
    const { setRefreshFunction } = useRefresh();
    const [files, setFiles] = useState<DatFile[]>([]);
    const [isPending, startTransition] = useTransition();
    const [searchQuery, setSearchQuery] = useState('');
    const [currentPage, setCurrentPage] = useState(1);
    const [selectedFiles, setSelectedFiles] = useState<string[]>([]);
    const { toast } = useToast();
    const ITEMS_PER_PAGE = 10;

    const fetchFiles = useCallback((isManualRefresh = false) => {
        startTransition(async () => {
            const folderId = localStorage.getItem('folderId');
            if (!folderId) {
                toast({ title: 'Error', description: 'User folder not found. Please log in again.', variant: 'destructive' });
                return;
            }
            const result = await getDatFiles(folderId);
            if (result.success && result.files) {
                setFiles(result.files);
                if (isManualRefresh) {
                    toast({ title: 'Refresh Successful', description: 'The file list has been updated.' });
                }
            } else {
                toast({ title: 'Error', description: result.error || 'Failed to fetch DAT files.', variant: 'destructive' });
            }
        });
    }, [toast]);

    useEffect(() => {
        fetchFiles();
        setRefreshFunction(() => () => fetchFiles(true));
    }, [fetchFiles, setRefreshFunction]);

    useEffect(() => {
        setCurrentPage(1);
        setSelectedFiles([]);
    }, [searchQuery]);

    const filteredFiles = files
        .filter(file => file.name.toLowerCase().includes(searchQuery.toLowerCase()))
        .sort((a, b) => new Date(b.modifiedTime).getTime() - new Date(a.modifiedTime).getTime());

    const totalPages = Math.ceil(filteredFiles.length / ITEMS_PER_PAGE);
    const paginatedFiles = filteredFiles.slice(
        (currentPage - 1) * ITEMS_PER_PAGE,
        currentPage * ITEMS_PER_PAGE
    );
    
    const handlePreviousPage = () => {
        setCurrentPage((prev) => Math.max(prev - 1, 1));
        setSelectedFiles([]);
    };

    const handleNextPage = () => {
        setCurrentPage((prev) => Math.min(prev + 1, totalPages));
        setSelectedFiles([]);
    };

    const handleSelectRow = (fileId: string, checked: boolean) => {
        if (checked) {
            setSelectedFiles(prev => [...prev, fileId]);
        } else {
            setSelectedFiles(prev => prev.filter(id => id !== fileId));
        }
    };
    
    const parseFileName = (fileName: string) => {
        const typeCode = fileName.charAt(9);
        const month = fileName.substring(10, 12);
        const year = fileName.substring(12, 16);

        let transactionType = 'Unknown';
        switch (typeCode) {
            case 'S': transactionType = 'Sales'; break;
            case 'P': transactionType = 'Purchases'; break;
            case 'I': transactionType = 'Importations'; break;
        }

        const reportingPeriod = new Date(parseInt(year), parseInt(month) - 1).toLocaleString('default', { month: 'long', year: 'numeric' });

        return { transactionType, reportingPeriod };
    };

    const noResults = !isPending && paginatedFiles.length === 0;

    return (
        <div className="p-4 md:p-6 space-y-6">
            <h1 className="text-3xl font-bold">DAT Files</h1>
            <p className="text-muted-foreground">Manage your generated DAT files.</p>
            <Card>
                <CardHeader>
                    <div className="flex items-center gap-4 pt-2">
                        <div className="relative flex-1">
                            <Search className="absolute left-2.5 top-2.5 h-4 w-4 text-muted-foreground" />
                            <Input
                                placeholder="Search by filename..."
                                className="pl-8"
                                value={searchQuery}
                                onChange={(e) => setSearchQuery(e.target.value)}
                            />
                        </div>
                    </div>
                </CardHeader>
                <CardContent>
                    {isPending ? (
                         <div className="h-48 text-center flex items-center justify-center">
                            <div className="flex items-center justify-center gap-2">
                                <Loader2 className="h-6 w-6 animate-spin text-primary" />
                                <p className="text-muted-foreground">Loading files...</p>
                            </div>
                        </div>
                    ) : noResults ? (
                        <div className="h-48 text-center flex flex-col items-center justify-center gap-4">
                           <SearchX className="h-12 w-12 text-muted-foreground" />
                            <h3 className="text-xl font-semibold">No DAT Files Found</h3>
                            <p className="text-muted-foreground">
                                {searchQuery ? "No files matched your search." : "You haven't generated any DAT files yet."}
                            </p>
                        </div>
                    ) : (
                       <>
                         {/* Desktop Table View */}
                        <div className="border rounded-md hidden md:block">
                            <Table>
                                <TableHeader>
                                    <TableRow>
                                        <TableHead className="w-[5%]">
                                        </TableHead>
                                        <TableHead className="w-[35%]">File Name</TableHead>
                                        <TableHead className="w-[20%]">Transaction Type</TableHead>
                                        <TableHead className="w-[20%]">Reporting Period</TableHead>
                                        <TableHead className="w-[10%]">Last Modified</TableHead>
                                        <TableHead className="w-[10%] text-right">Actions</TableHead>
                                    </TableRow>
                                </TableHeader>
                                <TableBody>
                                    {paginatedFiles.map((file) => {
                                        const { transactionType, reportingPeriod } = parseFileName(file.name);
                                        return (
                                            <TableRow key={file.id} data-state={selectedFiles.includes(file.id) && "selected"}>
                                                <TableCell>
                                                        <Checkbox
                                                        checked={selectedFiles.includes(file.id)}
                                                        onCheckedChange={(checked) => handleSelectRow(file.id, Boolean(checked))}
                                                        aria-label="Select row"
                                                    />
                                                </TableCell>
                                                <TableCell className="font-mono">{file.name}</TableCell>
                                                <TableCell>
                                                    <Badge variant={transactionType === 'Sales' ? 'default' : 'secondary'}>{transactionType}</Badge>
                                                </TableCell>
                                                <TableCell>{reportingPeriod}</TableCell>
                                                <TableCell>{format(new Date(file.modifiedTime), 'PP')}</TableCell>
                                                <TableCell>
                                                    <div className="flex items-center justify-end gap-2">
                                                        <Button variant="ghost" size="icon" onClick={() => console.log('View', file.id)}>
                                                            <Eye className="h-4 w-4" />
                                                            <span className="sr-only">View</span>
                                                        </Button>
                                                        <Button variant="ghost" size="icon" onClick={() => console.log('Download', file.id)}>
                                                            <FileDown className="h-4 w-4" />
                                                            <span className="sr-only">Download</span>
                                                        </Button>
                                                        <Button variant="ghost" size="icon" className="text-destructive hover:text-destructive" onClick={() => console.log('Delete', file.id)}>
                                                            <Trash className="h-4 w-4" />
                                                            <span className="sr-only">Delete</span>
                                                        </Button>
                                                    </div>
                                                </TableCell>
                                            </TableRow>
                                        );
                                    })}
                                </TableBody>
                            </Table>
                        </div>
                        
                        {/* Mobile Card View */}
                        <div className="block md:hidden space-y-4">
                            {paginatedFiles.map((file) => {
                                const { transactionType, reportingPeriod } = parseFileName(file.name);
                                return (
                                <Card key={file.id} className="p-4" data-state={selectedFiles.includes(file.id) && "selected"}>
                                    <div className="flex items-center">
                                        <Checkbox
                                            checked={selectedFiles.includes(file.id)}
                                            onCheckedChange={(checked) => handleSelectRow(file.id, Boolean(checked))}
                                            aria-label="Select row"
                                            className="mr-4"
                                        />
                                        <div className="flex-1 overflow-hidden">
                                            <p className="font-semibold font-mono truncate" title={file.name}>{file.name}</p>
                                            <p className="text-sm text-muted-foreground">{reportingPeriod}</p>
                                            <div className="flex items-center gap-4 mt-3">
                                                <Badge variant={transactionType === 'Sales' ? 'default' : 'secondary'}>{transactionType}</Badge>
                                            </div>
                                        </div>
                                        <div className="flex flex-col items-center flex-shrink-0 ml-2">
                                            <Button variant="ghost" size="icon" className="h-8 w-8" onClick={() => console.log('View', file.id)}>
                                                <Eye className="h-4 w-4" />
                                                <span className="sr-only">View</span>
                                            </Button>
                                            <Button variant="ghost" size="icon" className="h-8 w-8" onClick={() => console.log('Download', file.id)}>
                                                <FileDown className="h-4 w-4" />
                                                <span className="sr-only">Download</span>
                                            </Button>
                                            <Button variant="ghost" size="icon" className="h-8 w-8 text-destructive hover:text-destructive" onClick={() => console.log('Delete', file.id)}>
                                                <Trash className="h-4 w-4" />
                                                <span className="sr-only">Delete</span>
                                            </Button>
                                        </div>
                                    </div>
                                </Card>
                                )
                            })}
                        </div>
                        
                        {filteredFiles.length > ITEMS_PER_PAGE && (
                            <div className="flex items-center justify-end space-x-2 py-4 px-0 md:px-4">
                                <span className="text-sm text-muted-foreground">
                                    Page {totalPages > 0 ? currentPage : 0} of {totalPages}
                                </span>
                                <Button
                                    variant="outline"
                                    size="sm"
                                    onClick={handlePreviousPage}
                                    disabled={currentPage <= 1}
                                >
                                    Previous
                                </Button>
                                <Button
                                    variant="outline"
                                    size="sm"
                                    onClick={handleNextPage}
                                    disabled={currentPage >= totalPages}
                                >
                                    Next
                                </Button>
                            </div>
                        )}
                       </>
                    )}
                </CardContent>
            </Card>
        </div>
    );
}

export default function DatFilesPage() {
    return <DatFilesContent />;
}
